import { Box } from "@chakra-ui/react"
import { DataGrid } from '@mui/x-data-grid';
import clsx from 'clsx';
import "../style/TableTheme.css";

export default function ResultsTable(props) {

    const selectCellClick = (e) => {
      let newJSON = props.uploadedJSON;
      newJSON[e.id].selection = !newJSON[e.id].selection;
      props.updateJSON(newJSON)
    }

    const COLUMN_HEADERS = [
      //Sets class for header theme
      { field: 'manufacturer', headerName: 'Manufacturer', flex: 1, 
          cellClassName: (params) => {
          if (params.value == null) {
            return '';
          }
          return clsx('th')}
      },
      { field: 'item_code', headerName: 'Item Code', flex: 1 },
      { field: 'comp_description', headerName: 'Competitor Description', flex: 2 },
      { field: 'our_description', headerName: 'Our Description', flex: 2 },
      { field: 'best_match', headerName: 'Best Match', flex: 1 },
      { field: 'selection', headerName: 'Selection', flex: 1, align: 'center',
          renderCell: (params) => {
              return (params.value ?
                <Box className=".select-button">Selected</Box>
                :
                <Box className=".select-button-unset">Choose</Box>)
          },
          cellClassName: (params) => {
            return (params.value ? clsx('button-cell'): clsx('button-cell-unset'))
          },
          disableClickEventBubbling: true,
      },
  ]
    
    return (
        <DataGrid
          rows={props.uploadedJSON}
          columns={COLUMN_HEADERS}
          initialState={{
            pagination: {
              paginationModel: { page: 0, pageSize: 5 },
            },
          }}
          pageSizeOptions={[5, 10]}
          onCellClick={(e) => {selectCellClick(e)}}
        />
    );
}